// reducers.js

// reducerreducer
import { combineReducers } from 'redux'
// 
import defaultState from './state'

// reducer
function pageTitle (state = defaultState.pageTitle, action) {
    // action
    switch (action.type) {
        case 'SET_PAGE_TITLE':
            return action.data
        default:
            return state
    }
}

function infoList (state = defaultState.infoList, action) {
    switch (action.type) {
        case 'SET_INFO_LIST':
            return action.data
        default:
            return state
    }
}

// reducer
export default combineReducers({
    pageTitle,
    infoList
})



