// index.js

// applyMiddleware: redux
// createStore: store
import { applyMiddleware, createStore } from 'redux'


import thunk from 'redux-thunk'

// reducer
import reducers from './reducers'

// store
let store = createStore(
    reducers,
    applyMiddleware(thunk)
)

export default store


